import React, { useEffect } from 'react';
import './AddNews.css';
import axios from 'axios';
import { useState } from 'react';
import $ from 'jquery';
import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import LazyLoad from 'react-lazy-load';

const parse = require('html-react-parser');

const AddNews = () => {
    const[category, setCategory] = useState('none');
    const[title, setTitle] = useState('');
    const[img, setImg] = useState('');
    const[content, setContent] = useState('');
    const[disabled, setDisabled] = useState(false);
    const[metaDescr, setMetaDescr] = useState('');
    const[metaKeywords, setMetaKeywords] = useState('');
    const[author, setAuthor] = useState('');

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    const addNews = (e) => {
        e.preventDefault();

        if(category === 'none') {
            alert('–í—ã–±–µ—Ä–∏—Ç–µ –∫–∞—Ç–µ–≥–æ—Ä–∏—é');
        }
        else if(title === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫');
        }
        else if(img === '') {
            alert('–í—ã–±–µ—Ä–∏—Ç–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –∏–ª–∏ –≤—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –Ω–µ–µ');
        }
        else if(metaDescr === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –ø–æ–ª–µ –¥–ª—è –º–µ—Ç–∞ —Ç–µ–≥–∞: description');
        }
        else if(metaKeywords === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –ø–æ–ª–µ –¥–ª—è –º–µ—Ç–∞ —Ç–µ–≥–∞: keywords');
        }
        else if(author === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –∏–º—è –∞–≤—Ç–æ—Ä–∞');
        }
        else if(content === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –∫–æ–Ω—Ç–µ–Ω—Ç –¥–ª—è –Ω–æ–≤–æ—Å—Ç–∏');
        }
        else {
            $('.preview').fadeIn();
            $('body').css({overflow: "hidden"});
        }
    }

    const rejectAdd = () => {
        $('.preview').fadeOut();
        $('body').css({overflow: "auto"});
    }

    const acceptAdd = () => {
        axios.post('/admin/addNews', {
            category,
            title,
            img,
            content,
            metaDescr,
            metaKeywords,
            author
        })
        .catch(err => {
            if(err) throw err;
        });

        $('select').prop({selectedIndex: '0'});
        document.querySelector('#newsSubmit').innerHTML = '‚úì';
        document.querySelector('#newsSubmit').setAttribute('disabled', 'disabled');
        document.querySelector('#newsCategory').setAttribute('disabled', 'disabled');
        document.querySelector('#newsTitle').setAttribute('disabled', 'disabled');
        document.querySelector('.fileText input[type="file"]').setAttribute('disabled', 'disabled');
        document.querySelector('#newsSubmit').style.background = '#18ba20';
        $('.preview').fadeOut();
        $('body').css({overflow: "auto"});
        setDisabled(true);
        setImg('');
        setTitle('');
        setContent('');
        setCategory('none');
        setMetaDescr('');
        setMetaKeywords('');
        setAuthor('');
        $('input').val('');
        $('.fileText div:last-child button').css({display: 'none'});

        setTimeout(() => {
            document.querySelector('#newsSubmit').innerHTML = '+';
            document.querySelector('#newsSubmit').removeAttribute('disabled');
            document.querySelector('#newsCategory').removeAttribute('disabled');
            document.querySelector('#newsTitle').removeAttribute('disabled');
            document.querySelector('#newsImg').removeAttribute('disabled');
            document.querySelector('.fileText input[type="file"]').removeAttribute('disabled');
            document.querySelector('#newsSubmit').style.background = 'rgba(204, 135, 45, 0.9)';
            setDisabled(false);
        }, 5000);
    }

    const selectImg = async (e) => {
        const formData = new FormData();
        formData.append('image', e.target.files[0]);
        const {data} = await axios.post('/upload', formData);
        
        setImg(data.url);
        document.querySelector('.fileText div input[type="text"]').value = data.url;
        document.querySelector('.fileText div input[type="text"]').setAttribute('disabled', 'disabled');
        document.querySelector('.fileText div input[type="file"]').setAttribute('disabled', 'disabled');
        $('.fileText div:last-child button').css({display: 'flex'});
    }

    const delImg = (e) => {
        e.preventDefault();
    
        axios.post('/delUpload', {
            path: img
        });
        setImg('');
        document.querySelector('.fileText div input[type="text"]').value = '';
        $('.fileText div:last-child button').css({display: 'none'});
        document.querySelector('.fileText div input[type="text"]').removeAttribute('disabled');
        document.querySelector('.fileText div input[type="file"]').removeAttribute('disabled');
    }

    return (
        <div id='addNews'>
            <form>
                <label htmlFor="newsCategory">–ö–∞—Ç–µ–≥–æ—Ä–∏—è:</label>
                <select onChange={(e) => {
                    setCategory(e.target.value);
                }} defaultValue={'none'}  name="newsCategory" id="newsCategory">
                    <option value="none" disabled>–ù–µ –≤—ã–±—Ä–∞–Ω–∞</option>
                    <option value="rpl">–†–ü–õ</option>
                    <option value="epl">–ê–ü–õ</option>
                    <option value="laliga">–õ–∞ –õ–∏–≥–∞</option>
                    <option value="seriea">–°–µ—Ä–∏—è –ê</option>
                    <option value="bundesliga">–ë—É–Ω–¥–µ—Å–ª–∏–≥–∞</option>
                    <option value="ligue1">–õ–∏–≥–∞ 1</option>
                    <option value="ucl">–õ–ß</option>
                    <option value="uel">–õ–ï</option>
                    <option value="uecl">–õ–ö</option>
                    <option value="eu-qualification">–ï–≤—Ä. –∫–≤–ª—Ñ.</option>
                    <option value="unl">–õ–ù</option>
                    <option value="wc">–ß–ú</option>
                    <option value="ec">–ß–ï</option>
                    <option value="other">–†–∞–∑–Ω–æ–µ</option>
                    <option value="blog">–ë–ª–æ–≥–∏</option>
                    <option value="video">–í–∏–¥–µ–æ</option>
                    <option value="transfer">–¢—Ä–∞–Ω—Å—Ñ–µ—Ä—ã</option>
                </select>
                <label htmlFor="newsTitle">–ó–∞–≥–æ–ª–æ–≤–æ–∫:</label>
                <input placeholder='–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫' onChange={(e) => {
                    setTitle(e.target.value);
                }} type="text" id='newsTitle' name='newsTitle' />
                <label htmlFor="newsImg">–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ:</label>
                <div className='fileText'>
                    <div>
                        <input type="file" onChange={selectImg} />
                        <input placeholder='–í—ã–±–µ—Ä–∏—Ç–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ª–∏–±–æ –≤—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É' onChange={(e) => {
                        setImg(e.target.value);
                    }} type="text" name='newsImg' id='newsImg' />
                    </div>
                    <div>
                        {img && <img loading="lazy" src={img} alt="preview" />}
                        <button onClick={delImg}>‚®Ø</button>
                    </div>
                </div>
                <label htmlFor="meta_description">–ú–µ—Ç–∞-—Ç—ç–≥: description</label>
                <input onChange={(e) => {setMetaDescr(e.target.value)}} placeholder='–ú–µ—Ç–∞-—Ç—ç–≥: description' type="text" id='meta_description' name='meta_description' />
                <label htmlFor="meta_keywords">–ú–µ—Ç–∞-—Ç—ç–≥: keywords</label>
                <input onChange={(e) => {setMetaKeywords(e.target.value)}} placeholder='–ú–µ—Ç–∞-—Ç—ç–≥: keywords' type="text" id='meta_keywords' name='meta_keywords' />
                <label htmlFor="author">–ê–≤—Ç–æ—Ä:</label>
                <input onChange={(e) => {setAuthor(e.target.value)}} placeholder='–ò–º—è –§–∞–º–∏–ª–∏—è' type="text" id='author' name='author' />
                <label id='newsContentLabel' htmlFor="newsContent">–ö–æ–Ω—Ç–µ–Ω—Ç:</label>
                <CKEditor config={{placeholder: "–í–≤–µ–¥–∏—Ç–µ –æ–ø–∏—Å–∞–Ω–∏–µ –Ω–æ–≤–æ—Å—Ç–∏", mediaEmbed: {previewsInData: true}}} data={content} disabled={disabled} id="newsContent" editor={ClassicEditor} onChange={(e, editor) => {
                    setContent(editor.getData());
                }} />
                <button title='–ü—Ä–µ–¥–ø—Ä–æ—Å–º–æ—Ç—Ä' type='submit' id='newsSubmit' onClick={addNews}>+</button>
            </form>

            <div className="preview">
                <p className="popupTitle">–ü—Ä–µ–¥–ø—Ä–æ—Å–º–æ—Ç—Ä</p>

                <section>
                    <div className="container">
                        <p className="pageName">{title}</p>
                        <span className="date">–î–î-–ú–ú-–ì–ì–ì–ì | –ß–ß:–ú–ú</span>
                        <LazyLoad offset={800}>
                            <img loading="lazy" id='mainImg' src={img} alt="newsImg" />
                        </LazyLoad>
                        <p><strong>{metaDescr}</strong></p>
                        <div className="textWrap">{parse(content)}</div>
                        <i className='author'>{author}</i>
                    </div>
                </section>

                <button title='–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å' type='submit' className='acceptBtn' onClick={acceptAdd}>‚úì</button>
                <button title='–û—Ç–∫–ª–æ–Ω–∏—Ç—å' type='submit' className='rejectBtn' onClick={rejectAdd}>‚®Ø</button>
            </div>
        </div>
    );
};

export default AddNews;