import React, { useEffect, useState } from 'react';
import './AddBroadcasts.css';
import axios from 'axios';
import $ from 'jquery';

const AddBroadcasts = (e) => {
    const[hName, setHName] = useState('');
    const[hLogo, setHLogo] = useState('');
    const[lName, setLName] = useState('');
    const[lLogo, setLLogo] = useState('');
    const[aName, setAName] = useState('');
    const[aLogo, setALogo] = useState('');
    const[time, setTime] = useState('');
    const[broadcastLink, setBroadcastLink] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    const add = (e) => {
        e.preventDefault();

        if(hName === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–∞–Ω–¥—ã —Ö–æ–∑—è–µ–≤–∞');
        }
        else if(hLogo === '') {
            alert('–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ –∫–æ–º–∞–Ω–¥—ã —Ö–æ–∑—è–µ–≤–∞');
        }
        else if(lName === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ç—É—Ä–Ω–∏—Ä–∞');
        }
        else if(lLogo === '') {
            alert('–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ —Ç—É—Ä–Ω–∏—Ä–∞');
        }
        else if(aName === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Å—Ç–µ–≤–æ–π –∫–æ–º–∞–Ω–¥—ã');
        }
        else if(aLogo === '') {
            alert('–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ –≥–æ—Å—Ç–µ–≤–æ–π –∫–æ–º–∞–Ω–¥—ã');
        }
        else if(time === '') {
            alert('–í–≤–µ–¥–∏—Ç–µ –≤—Ä–µ–º—è –Ω–∞—á–∞–ª–æ –º–∞—Ç—á–∞');
        }
        else {
            axios.post('/broadcasts/add', {
                hName,
                hLogo,
                lName,
                lLogo,
                aName,
                aLogo, 
                time,
                broadcastLink
            });
    
            document.querySelector('#addBroadcast button').style.background = '#18ba20';
            document.querySelector('#addBroadcast button').innerHTML = '‚úì';
            document.querySelector('#addBroadcast button').setAttribute('disabled', 'disabled');
            document.querySelector('#addBroadcast button').style.color = '#fff';
            $('#addBroadcast form div input').attr('disabled', 'disabled');
            $('#addBroadcast form div input').val('');

            setHName('');
            setHLogo('');
            setLName('');
            setLLogo('');
            setAName('');
            setALogo('');
            setTime('');
            setBroadcastLink();
            
            setTimeout(() => {
                document.querySelector('#addBroadcast button').style.background = '#ffbf66';
                document.querySelector('#addBroadcast button').innerHTML = '+';
                document.querySelector('#addBroadcast button').removeAttribute('disabled', 'disabled');
                $('#addBroadcast form div input').removeAttr('disabled', 'disabled');
                document.querySelector('#addBroadcast button').style.color = '#000';
            }, 10000);
        }
    }

    return (
        <div id='addBroadcast'>
            <form className="wrap">
                <div>
                    <label htmlFor="name1">–ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–∞–Ω–¥—ã 1</label>
                    <input id='name1' type="text" placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–∞–Ω–¥—ã —Ö–æ–∑—è–µ–≤–∞' onChange={(e) => {setHName(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="logo1">–õ–æ–≥–æ –∫–æ–º–∞–Ω–¥—ã 1</label>
                    <input id='logo1' type="text" placeholder='–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ –∫–æ–º–∞–Ω–¥—ã —Ö–æ–∑—è–µ–≤–∞' onChange={(e) => {setHLogo(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="lName">–ù–∞–∑–≤–∞–Ω–∏–µ —Ç—É—Ä–Ω–∏—Ä–∞</label>
                    <input id='lName' type="text" placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ç—É—Ä–Ω–∏—Ä–∞' onChange={(e) => {setLName(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="lLogo">–õ–æ–≥–æ —Ç—É—Ä–Ω–∏—Ä–∞</label>
                    <input id='lLogo' type="text" placeholder='–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ —Ç—É—Ä–Ω–∏—Ä–∞' onChange={(e) => {setLLogo(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="name2">–ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–º–∞–Ω–¥—ã 2</label>
                    <input id='name2' type="text" placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Å—Ç–µ–≤–æ–π –∫–æ–º–∞–Ω–¥—ã' onChange={(e) => {setAName(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="logo2">–õ–æ–≥–æ –∫–æ–º–∞–Ω–¥—ã 2</label>
                    <input id='logo2' type="text" placeholder='–í—Å—Ç–∞–≤—å—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ª–æ–≥–æ –≥–æ—Å—Ç–µ–≤–æ–π –∫–æ–º–∞–Ω–¥—ã' onChange={(e) => {setALogo(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="time">–í—Ä–µ–º—è</label>
                    <input id='time' type="text" placeholder='–í–≤–µ–¥–∏—Ç–µ –≤—Ä–µ–º—è –Ω–∞—á–∞–ª–æ –º–∞—Ç—á–∞' onChange={(e) => {setTime(e.target.value)}} />
                </div>
                <div>
                    <label htmlFor="time">–°—Å—ã–ª–∫–∞ –Ω–∞ —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—é *–º–æ–∂–Ω–æ –∏ –ø–æ–∑–∂–µ</label>
                    <input id='time' type="text" placeholder='–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ —Ç—Ä–∞–Ω—Å–ª—è—Ü–∏—é' onChange={(e) => {setBroadcastLink(e.target.value)}} />
                </div>

                <button type='submit' onClick={add}>+</button>
            </form>
        </div>
    );
};

export default AddBroadcasts;